.sign-in_wrapper {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 99;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background: #fff;
    width: 90vw;
    max-width: 1000px;
    height: 80vh;
    max-height: 700px;
    box-shadow: rgba(0,0,0,0.24) 0px 2px 2px 0px, rgba(0,0,0,.24) 0px 0px 1px 0px;


    .sign-in_left-side {
        min-width: 400px;
        height: 100%;
        background-image: url(../assets/girl-in-plane.jpg);
        background-size: cover;
    }
    .sign-in_right-side {
        .sign-in_title {
            text-align: center;
            font-weight: 600;
            font-size: 24px;
            color: #222;
            margin-bottom: 50px;
        }
        .sign-in_social-media_wrapper {
            display: flex;
            justify-content: center;
            align-items: center;

            .google_login {
                margin-right: 20px;
                span {
                    color: #000;
                    font-size: 16px;
                }
            }
            .facebook_login {
                    height: 42px;
                    font-size: 15px;
                    box-shadow: rgba(0,0,0,0.24) 0px 2px 2px 0px, rgba(0,0,0,.24) 0px 0px 1px 0px;
                    line-height: 0px;
                    background: #3b5998;
                    color: #fff;
                    font-family: Helvetica,sans-serif;
                    font-weight: 700;
                    -webkit-font-smoothing: antialiased;
                    cursor: pointer;
                    border: none;
                    width: 200px;

                    i {
                        margin-right: 10px;
                    }
            }
        }
        .sign-in_create-account_link {
            text-align: center;
            margin-top: 50px;
            a {
                color: blue;
            }
        }
    }

}
.pop-up_background {
    position: fixed;
    z-index: 0;
    top: 0;
    height: 120vh;
    left: 0;
    right: 0;
    background: rgba(30,30,30,.5);
}