.navbar_wrapper {  
    position: absolute;
    top: 0;
    width: 100vw;  
    z-index: 99;
    .navbar {
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        max-width: 1200px;
        margin: 0 auto;
        padding: 10px 0px;
        ul {
            display: flex;
            li {
                list-style: none;
                margin: 5px 10px;
                padding: 5px;
                
                a {
                    position: relative;
                    color: #fff;
                    font-weight: 600;
                    text-decoration: none;
                    border-radius: 25px;
                    padding: 2px 10px;
                    transition-duration: 300ms;
                    transition: all forwards;
                    opacity: .8;
                    
                    &:hover {
                        background: rgba(255,255,255,.9);
                        color: #333;
                        opacity: 1;
                    }
                }
            
            }
        }

        .navbar_left {
            
        }
        .navbar_center {
            .navbar_logo {
                height: 50px;
                border-radius: 50%;
            }
        }
        .navbar_right {
            
        }
    }
}

