.homepage_wrapper {
    background: #333333;
    width: 100vw;
    min-height: 100vh;

    .homepage {
        .homepage_hero {
            position: relative;
            height: 100vh;
            max-height: 900px;

            .homepage_left-content {
                position: absolute;
                z-index: 2;
                top: 50%;
                left: 20%;
                transform: translate(0, -50%);
                padding: 10px;
                color: #fff;

                h1 {
                    font-size: 90px;
                    margin-bottom: 20px;
                    line-height: 120px;
                    letter-spacing: 8px;
                    font-family: 'Rock Salt', cursive;
                    text-shadow: 1px 1px 3px #666;
                }
                h2 {
                    font-weight: 100;
                    font-size: 20px;
                    margin-top: 5px;
                    
                    strong {
                        font-weight: 800;
                    }
                }

                .homepage_hero_cta {
                    position: relative;
                    display: inline-block;
                    margin-top: 25px;
                    background: rgba(79, 133, 86, 0.8);
                    color: #fff;
                    text-decoration: none;
                    font-size: 20px;
                    font-weight: 500;
                    padding: 8px 60px;
                    border-radius: 5px;
                    transition: all 300ms;
                    
                    &:hover {
                        background: rgba(79, 133, 86, 1);
                    }
                }
            }


            .hero_img {
                position: absolute;
                top: 0;
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        .homepage_how-it-works {
            background: #fff;
            height: 100vh;
            max-height: 700px;

        }
    }
}